/* chat.module.css */

/* Geral */
.container {
  display: flex;
  height: 100vh;
  font-family: 'Arial', sans-serif;
  background-color: var(--bg-color);
  color: var(--text-color);
}

/* Sidebar */
.sidebar {
  width: 250px;
  border-right: 1px solid #ccc;
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: var(--sidebar-bg);
}

.user-profile {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.user-profile img {
  border-radius: 50%;
  margin-right: 10px;
  width: 50px;
  height: 50px;
}

.user-list {
  list-style: none;
  padding: 0;
}

.user-list li {
  padding: 8px;
  cursor: pointer;
  border-radius: 8px;
  margin-bottom: 5px;
}

.user-list li.selected {
  background-color: #eee;
}

/* Botões */
.sidebar button {
  border-radius: 8px;
  padding: 8px;
  margin-bottom: 5px;
  border: none;
  cursor: pointer;
  background-color: #4caf50;
  color: white;
  transition: background 0.3s;
}

.sidebar button:hover {
  background-color: #45a049;
}

/* Área principal */
.main {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.messages {
  flex: 1;
  padding: 10px;
  overflow-y: auto;
  border-bottom: 1px solid #ccc;
}

.message {
  margin: 5px 0;
  padding: 5px 10px;
  display: inline-block;
  border-radius: 10px;
  max-width: 60%;
}

.message.sent {
  text-align: right;
  background-color: #DCF8C6;
  align-self: flex-end;
}

.message.received {
  text-align: left;
  background-color: #fff;
  align-self: flex-start;
}

.input-container {
  display: flex;
  padding: 10px;
}

.input-container input {
  flex: 1;
  padding: 8px;
  border-radius: 8px;
  border: 1px solid #ccc;
  margin-right: 5px;
}

.input-container button {
  border-radius: 8px;
  padding: 8px;
  border: none;
  background-color: #4caf50;
  color: white;
  cursor: pointer;
  transition: background 0.3s;
}

.input-container button:hover {
  background-color: #45a049;
}

/* Configurações */
.config-screen, .profile-screen, .voice-screen, .add-contact-screen {
  padding: 20px;
}

/* Tema claro e escuro */
:root {
  --bg-color: #f2f2f2;
  --text-color: #000;
  --sidebar-bg: #fff;
}

.dark-theme {
  --bg-color: #1c1c1c;
  --text-color: #f2f2f2;
  --sidebar-bg: #2c2c2c;
}
